<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="false" ModuleName="" SubModuleCode="" :LegendArray="[]"
        :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
        IdentityColumn="AVAILABILITY_ID" :listType="['List']" :ListData="UserAvailabilityData" :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
        IsSearchFieldsOnTop="true" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :HeaderText="$t('UserAvailability')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">

        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'AVAILABILITY_START_DATE'">
                <span>
                {{ $options.filters.formatDate(data.row.AVAILABILITY_START_DATE) }}
            </span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'RECURRING_TILL_DATE'">
                <span>
                {{ $options.filters.formatDate(data.row.RECURRING_TILL_DATE) }}
            </span>
            </template>        
            <template v-if="data.column.COLUMN_NAME == 'AVAILABILITY_START_TIME'">
                <span>
                    {{ formatTimeWithoutSeconds(data.row.AVAILABILITY_START_TIME) }}
                </span>
            </template>    
            <template v-if="data.column.COLUMN_NAME == 'AVAILABILITY_END_TIME'">
                <span>
                    {{ formatTimeWithoutSeconds(data.row.AVAILABILITY_END_TIME) }}
                </span>
            </template>       
            <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')" v-if="data.row.STATUS_NAME == 'Available'">
                {{ data.row[data.column.COLUMN_NAME] }}
                </span>
                <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')" v-if="data.row.STATUS_NAME == 'Busy'">
                    {{ data.row[data.column.COLUMN_NAME] }}
                </span>
            </template>
        </template>

    </tg-list>
</section>
</template>

<script>
import DataService from "@/services/DataService";
import Schema from "@/views/Event/UserAvailability/Schema";

export default {
    data() {
        return {
            isLoading: false,
            hour12: false,
            Headers: [{
                    "COLUMN_NAME": "AVAILABILITY_TITLE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Title',
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "AVAILABILITY_START_DATE",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Start Date',
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "RECURRING_TILL_DATE",
                    "DATA_TYPE": "Date",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Till Date',
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "AVAILABILITY_START_TIME",
                    "DATA_TYPE": "Date",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Start Time',
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "AVAILABILITY_END_TIME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'End Time',
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "STATUS_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Status',
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true
                    }
                }
            ],
            SortBy: '',
            SortExp: '',
            PageSize: "20",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecords: false,
            searchCondition: '',
            UserAvailabilityData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            leftSearchFields: Schema.leftsearchSchema,
            searchBy: '',
            search: ''
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        formatTimeWithoutSeconds(time) {
            const date = new Date(time);
            return new Intl.DateTimeFormat('en-US', {
                hour: '2-digit',
                minute: '2-digit',
                hour12: true,
            }).format(date);
        },
        actionButtonClick: function (callbackfunction, item) {},
        leftCommonSearch: function (value) {
            var vm = this;
            vm.search = '';
            vm.searchBy = '';
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            var searchCondition = value.searchCondition;

            if (typeof value.searchCondition != 'undefined') {
                if (searchCondition.includes('SearchBy') || searchCondition.includes('Search')) {
                    let searchByRegex = /SearchBy\s*=\s*'([^']+)'|SearchBy\s*in\s*\(([^)]+)\)/;
                    let searchByMatch = searchCondition.match(searchByRegex);

                    if (searchByMatch) {
                        let searchByValue = searchByMatch[1] || searchByMatch[2];
                        searchByValue = searchByValue.replace(/^'+|'+$/g, '');
                        vm.searchBy = searchByValue;
                    }

                    let searchValueRegex = /Search\s*LIKE\s*'%([^%]+)%'/;
                    let searchValueMatch = searchCondition.match(searchValueRegex);
                    let searchValue = searchValueMatch ? searchValueMatch[1] : null;
                    vm.search = searchValue == null ? '' : searchValue;
                }
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "AddUserAvailability":
                    this.AddUserAvailability();
                    break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        AddUserAvailability: function () {
            var vm = this;
            vm.$router.push({
                name: 'AddUserAvailability',
                params: {
                    id: 0
                }
            });
        },
        clickEventOfColumn: function (value, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'AVAILABILITY_TITLE') {
                vm.$router.push({
                    name: 'AddUserAvailability',
                    params: {
                        id: value.AVAILABILITY_ID
                    }
                });
            }
        },
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;
            if (vm.search != '' || vm.searchBy != '') {
                var params = `Title=${vm.search}&status=${vm.searchBy}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`
            } else {
                var params = `Title=&status=&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`
            }
            await DataService.GetUserAvailabilityListing(params).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.isLoading = false;
                    vm.UserAvailabilityData = response.data;                

                    vm.Headers.forEach((column) => {
                        column.DISPLAY_NAME = vm.$t(column.DISPLAY_NAME ? column.DISPLAY_NAME : column.COLUMN_NAME);
                        column.settings = null;
                        if (column.COLUMN_NAME == 'AVAILABILITY_TITLE') {
                            column.settings = {
                                clickEvent: vm.clickEventOfColumn,
                                formatter: vm.columnDataFormatter
                            }
                        }
                        if (column.COLUMN_NAME == "AVAILABILITY_START_DATE" || column.COLUMN_NAME == "RECURRING_TILL_DATE" || column.COLUMN_NAME == "AVAILABILITY_START_TIME" || column.COLUMN_NAME == 'AVAILABILITY_END_TIME' || column.COLUMN_NAME == 'STATUS_NAME') {
                            column.settings = {
                                isInSlot: true,
                            }
                        }
                    })

                    vm.TotalRecords = response.data[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.listheaderbuttons[1].isdisabled = true;
                    vm.noRecords = false;
                } else {
                    vm.UserAvailabilityData = [];
                    vm.noRecords = true;
                    vm.TotalRecords = 0
                    vm.isLoading = false;
                }
            })
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("UserAvailability")), function () {
                    var url = `Ids=${val}&statusId=1003`;
                    DataService.GetUserStatus(url).then(function (response) {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('UserAvailability')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('UserAvailability')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Available':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Busy':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        }
    }
}
</script>